<template>
  <div>
    <div class="row-header no__hidden_class">
      <div class="background-triangle-big">
        <span>H THNG GI S T NG</span>
      </div>
      <div class="layout row wrap header_tools row-blue">
      </div> 
    </div>
    <v-card flat color="#ffffff" class="pt-2 pb-4">
      <v-flex xs12 class="text-xs-center pt-2" id="nav-calling">
        <v-layout wrap class="white py-0">
          <v-flex xs6 class="px-2">
            <v-autocomplete
              class="select-border"
              :items="serviceInfoList"
              v-model="serviceInfoSelected"
              label="Chn th tc"
              item-text="serviceName"
              item-value="serviceCode"
              :hide-selected="true"
              clearable
              @change="changeService"
              box
              :disabled="isCalling"
            ></v-autocomplete>
          </v-flex>
          <v-flex xs6 class="px-2">
            <v-autocomplete
              class="select-border"
              :items="stateList"
              v-model="stateSelected"
              label="Chn trng thi"
              item-text="stateName"
              item-value="stateCode"
              :hide-selected="true"
              clearable
              @change="changeState"
              box
              :disabled="isCalling"
            ></v-autocomplete>
          </v-flex>
        </v-layout>
        <v-chip class="text-bold" color="#3fa8f1" text-color="white" style="width:170px;height:42px;font-size: 22px;position:absolute;top:100px;left:10px">
          <v-avatar class="pl-3">
            <v-icon color="white"> chrome_reader_mode</v-icon>
          </v-avatar>
          <span class="pl-2" v-if="!currentGate">BN S ---</span>
          <span class="pl-2" v-else>BN S {{currentGate}}</span>
        </v-chip>
        <v-flex xs12 class="text-xs-center" style="position: relative;">
          <div class="d-inline-block">
            <div class="d-inline-block">
              <h1 class="my-2" style="color: green">S ANG GI</h1>
              <v-chip class="d-inline-block text-xs-center pt-2" label color="#3fa8f1" text-color="white" style="width:250px;height:70px;font-size:32px;color:#ffffff">
                <span v-if="currentBooking">{{currentBooking.codeNumber}}</span>
                <span v-else>-- -- --</span>
              </v-chip>
            </div>
            <div v-if="currentBooking" class="d-inline-block ml-3" style="position: absolute;top:50px">
              <v-btn
                :loading="loadingCalling"
                :disabled="loadingCalling || isCalling"
                color="red"
                class="white--text"
                @click="ignoreBooking"
              >
                <v-icon class="ml-0" right dark>clear</v-icon> &nbsp;
                B QUA
              </v-btn>
              <br>
              <v-btn
                :loading="loadingCalling"
                :disabled="loadingCalling || isCalling"
                color="primary"
                class="white--text mt-1"
                @click="callBack"
              >
                <v-icon class="ml-0" right dark>cached</v-icon> &nbsp;
                GI LI
              </v-btn>
            </div>
          </div>
        </v-flex>
        <!--  -->
        <v-layout wrap>
          <v-flex xs6 class="">
            <div class="text-bold my-2 mt-4 mx-2 left" style="text-transform:uppercase">
              Danh sch {{getStateName()}}
            </div>
          </v-flex>
          <v-flex xs6 class="text-right pt-3">
            <div class="d-inline-block">
              <span class="text-bold">BN TIP NHN S:</span>
              <v-select
                :items="gateList"
                v-model="currentGate"
                item-text="value"
                item-value="value"
                :hide-selected="true"
                clearable
                class="d-inline-block ml-3 text-bold"
                style="width:100px;font-size:16px"
              >
              </v-select>
            </div>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-data-table
        id="table-danhsachchoMC"
        :headers="headers"
        :items="bookingList"
        hide-actions
        class="mt-2"
        style="border-left: 1px solid #dedede; margin-top: 10px;"
      >
        <template slot="items" slot-scope="props">
          <tr v-bind:class="{'active': props.index%2==1}" style="cursor: pointer;">
            <td class="text-xs-center text-bold py-2" width="70px">
              <div>
                <span>{{props.index + 1}}</span><br>
              </div>
            </td>
            <td class="text-xs-center text-bold py-2" width="150px">
              <div>
                <span>{{props.item.codeNumber}}</span>
              </div>
            </td>
            <td class="text-xs-left text-bold py-2 px-5">
              <div>
                <span>{{props.item.bookingName}}</span>
              </div>
            </td>
            <td class="text-xs-center text-bold py-1" width="350px">
              <v-btn
                :loading="loadingCalling"
                :disabled="loadingCalling || currentBooking['codeNumber'] === props.item['codeNumber'] || props.item['state'] != 1 || isCalling"
                :color="currentBooking['codeNumber'] === props.item['codeNumber'] || props.item['state'] != 1  ? 'grey' : 'primary'"
                class="white--text"
                @click="callingApplicant(props.item)"
                style="width: 100px;"
              >
                <span v-if="currentBooking['codeNumber'] === props.item['codeNumber'] || props.item['state'] == 2">ang gi...</span>
                <span v-else>Gi s</span>
              </v-btn>
              <v-btn
                :loading="loadingCalling"
                :disabled="loadingCalling || currentBooking['codeNumber'] === props.item['codeNumber'] || 
                props.item['state'] === 4 || props.item['state'] === 3 || isCalling"
                :color="currentBooking['codeNumber'] === props.item['codeNumber'] ? 'grey' : 'red'"
                class="white--text ml-2"
                @click="ignoreBooking(props.item)"
                style="width: 100px;"
              >
                B qua
              </v-btn>
              <v-btn
                :loading="loadingCalling"
                :disabled="loadingCalling || props.item['state'] === 4"
                color="#3fa8f1"
                class="white--text ml-2"
                @click="receiveBooking(props.item)"
                style="width: 100px;"
              >
                Tip nhn
              </v-btn>
            </td>
          </tr>
        </template>
        <template slot="no-data">
          <v-card flat color="#fff">
            <v-flex class="text-xs-center">
              <span>Khng c lt ch no</span>
            </v-flex>
          </v-card>
        </template>
      </v-data-table>
      <!--  -->
      <div id="audioCalling" style="visibility: hidden;position:fixed;top:0;right:0;z-index:-999">
      </div>
    </v-card>

    <v-dialog v-model="dialogAction" max-width="550" transition="fade-transition" persistent>
      <v-card>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-toolbar dark color="primary">
            <v-toolbar-title>La chn dch v</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon dark @click.native="dialogAction = false">
              <v-icon>close</v-icon>
            </v-btn>
          </v-toolbar>
          <v-card-text class="pb-0 pt-4">
            <v-layout wrap>
              <v-flex xs12 class="px-2">
                <v-select
                  :items="listDichVu"
                  v-model="dichVuSelected"
                  label="Dch v:"
                  item-text="optionName"
                  item-value="processOptionId"
                  return-object
                  :hide-selected="true"
                  :rules="[v => !!v || 'Dch v bt buc phi chn.']"
                  required
                  box
                ></v-select>
              </v-flex>
            </v-layout>
          </v-card-text>
          <v-card-actions class="mx-3">
            <v-spacer></v-spacer>
            <v-btn color="red darken-3" flat="flat" @click.native="dialogAction = false"
            >
              <v-icon>reply</v-icon>&nbsp;
              Thot
            </v-btn>
            <v-btn color="primary" flat="flat" @click.native="doSubmitCreateDossier()"
              :loading="loadingAction"
              :disabled="loadingAction"
            >
              <v-icon>save</v-icon>&nbsp;
              ng 
              <span slot="loader">Loading...</span>
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

import Vue from 'vue'
import $ from 'jquery'
import axios from 'axios'
import toastr from 'toastr'
import support from '../../store/support.json'
import TinyPagination from './Pagination.vue'
Vue.use(toastr)
export default {
  props: [],
  components: {
    'tiny-pagination': TinyPagination
  },
  data: () => ({
    loadData: false,
    dialogAction: false,
    dichVuSelected: '',
    listDichVu: '',
    filterCreateDossier: '',
    groupId: window.themeDisplay.getScopeGroupId(),
    idVoicePortlet: 161427,
    serviceInfoList: [],
    serviceInfoSelected: '',
    stateList: [
      {
        stateName: 'ang ch gi',
        stateCode: '1'
      },
      {
        stateName: 'ang c gi',
        stateCode: '2'
      },
      {
        stateName: 'Hy tip nhn',
        stateCode: '3'
      },
      {
        stateName: ' x l xong',
        stateCode: '4'
      }
    ],
    stateSelected: '1',
    loadingCalling: false,
    gateList: [{
      value: 1,
      text: 'Bn s 1'
    },
    {
      value: 2,
      text: 'Bn s 2'
    },
    {
      value: 3,
      text: 'Bn s 3'
    },
    {
      value: 4,
      text: 'Bn s 4'
    },
    {
      value: 5,
      text: 'Bn s 5'
    }],
    currentGate: '',
    bookingList: [
    ],
    loading: true,
    headers: [
      {
        text: 'Th t',
        align: 'center',
        sortable: false
      },
      {
        text: 'M t khai',
        align: 'center',
        sortable: false
      },
      {
        text: 'Ngi np h s',
        align: 'center',
        sortable: false
      },
      {
        text: 'Thao tc',
        align: 'center',
        sortable: false
      }
    ],
    currentBooking : '',
    isCalling: false
  }),
  computed: {
  },
  created () {
    var vm = this
    vm.$nextTick(function () {
      let current = vm.$router.history.current
      let currentQuery = current.query
      vm.getServiceInfo()
      vm.loadBooking()
      setTimeout(function () {
        if (vm.bookingList.length > 5) {
          let heightScroll = $('header').height() + $('.navbar-container').height()
          let widthParent = $('#nav-calling').parent().width()
          $(window).scroll(function () {
            if ($(window).scrollTop() > heightScroll) {
              $('#nav-calling').css('width',`${widthParent}px`)
              $('#nav-calling').addClass('fixedContent')
            } else {
              $('#nav-calling').removeClass('fixedContent')
            }
          })
        }
      }, 500)
    })
  },
  updated () {
    var vm = this
    vm.$nextTick(function () {
    })
  },
  watch: {
    '$route': function (newRoute, oldRoute) {
      let vm = this
      let currentParams = newRoute.params
      let currentQuery = newRoute.query
      vm.loadBooking()
    },
    loadData (val) {
      let vm = this
      setTimeout(function () {
        vm.loadBooking()
      }, 15000)
    },
    currentGate (val) {
      let vm = this
      vm.updateGateNumber(val)
    }
  },
  methods: {
    getServiceInfo () {
      let vm = this
      let current = vm.$router.history.current
      let newQuery = current.query
      vm.$store.dispatch('getServiceLists').then(function (result) {
        vm.serviceInfoList = result
        vm.serviceInfoSelected = newQuery.hasOwnProperty('service') && newQuery.service ? newQuery.service : ''
      })
    },
    filterBooking () {
      let vm = this
      setTimeout(function () {
        let current = vm.$router.history.current
        let newQuery = current.query
        let queryString = '?'
        newQuery['service'] = vm.serviceInfoSelected
        newQuery['state'] = vm.stateSelected
        for (let key in newQuery) {
          if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null) {
            queryString += key + '=' + newQuery[key] + '&'
          }
        }
        vm.$router.push({
          path: current.path + queryString,
          query: {
            renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
          }
        })
      }, 200)
    },
    changeService () {
      var vm = this
      vm.filterBooking()
    },
    changeState () {
      var vm = this
      vm.filterBooking()
    },
    loadBooking () {
      var vm = this
      let count = 0
      vm.loading = true
      let currentQuery = vm.$router.history.current.query
      let filterEform = {
        service: currentQuery.hasOwnProperty('service') ? currentQuery.service : vm.serviceInfoSelected,
        state: currentQuery.hasOwnProperty('state') ? currentQuery.state : vm.stateSelected,
        className: 'EFORM'
      }
      let bookingDossier = ''
      let bookingEform = ''
      vm.$store.dispatch('getBooking', filterEform).then(function (result) {
        count+=1
        vm.loading = false
        if (result.data) {
          bookingEform = result.data
        }
        if (count === 2) {
          vm.mergeBooking(bookingDossier, bookingEform)
        }
      }).catch(reject => {
        count+=1
        if (count === 2) {
          vm.mergeBooking(bookingDossier, bookingEform)
        }
        vm.loading = false
      })
      let filterDossier = {
        service: currentQuery.hasOwnProperty('service') ? currentQuery.service : vm.serviceInfoSelected,
        state: currentQuery.hasOwnProperty('state') ? currentQuery.state : vm.stateSelected,
        className: 'DOSSIER'
      }
      vm.$store.dispatch('getBooking', filterDossier).then(function (result) {
        count+=1
        vm.loading = false
        if (result.data) {
          bookingDossier = result.data
        }
        if (count === 2) {
          vm.mergeBooking(bookingDossier, bookingEform)
        }
      }).catch(reject => {
        count+=1
        vm.loading = false
        if (count === 2) {
          vm.mergeBooking(bookingDossier, bookingEform)
        }
      })
      
    },
    mergeBooking (bookingEform, bookingDossier) {
      let vm = this
      if (bookingEform || bookingDossier) {
        vm.bookingList = bookingEform.concat(bookingDossier)
        let sortBooking = function (bookingList) {
          function compare(a, b) {
            if (a.checkinDate < b.checkinDate)
              return -1
            if (a.checkinDate > b.checkinDate)
              return 1
            return 0
          }
          return bookingList.sort(compare)
        }
        vm.bookingList = sortBooking(vm.bookingList)
      }
      vm.loadData = !vm.loadData
    },
    paggingData (config) {
      let vm = this
      let current = vm.$router.history.current
      let newQuery = current.query
      let queryString = '?'
      newQuery['page'] = ''
      for (let key in newQuery) {
        if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null && newQuery[key] !== 'null') {
          queryString += key + '=' + newQuery[key] + '&'
        }
      }
      queryString += 'page=' + config.page
      vm.$router.push({
        path: current.path + queryString
      })
    },
    callingApplicant (item) {
      let vm = this
      if (vm.currentGate) {
        vm.currentBooking = item
        $('#audioCalling').html('')
        let splitNumberCode = vm.currentBooking['codeNumber'].split('-')
        let numberCalling = splitNumberCode[1] + splitNumberCode[2]
        let srcAudioStart = splitNumberCode[0] === 'E' ? `http://hanoi.fds.vn:1580/documents/${vm.groupId}/${vm.idVoicePortlet}/eformStart1.mp3` : `http://hanoi.fds.vn:1580/documents/${vm.groupId}/${vm.idVoicePortlet}/dossierStart1.mp3`
        let numberArr = String(numberCalling).split('')
        let audioStart = `
          <audio id="start" controls>
            <source src="${srcAudioStart}" type="audio/mp3">
          </audio>
        `
        let audioEnd = `
          <audio id="end">
            <source src="http://hanoi.fds.vn:1580/documents/${vm.groupId}/${vm.idVoicePortlet}/toGateNumber.mp3" type="audio/mp3">
          </audio>
        `
        let audioNumber = ''
        for (let index = 0; index < numberArr.length; index++) {
          audioNumber += `
            <audio id="au${index}">
              <source src="http://hanoi.fds.vn:1580/documents/${vm.groupId}/${vm.idVoicePortlet}/${numberArr[index]}.mp3" type="audio/mp3">
            </audio>
          `
        }
        let gateAudio = `
          <audio id="gateNumber">
            <source src="http://hanoi.fds.vn:1580/documents/${vm.groupId}/${vm.idVoicePortlet}/${vm.currentGate}.mp3" type="audio/mp3">
          </audio>
        `
        $('#audioCalling').html(audioStart + audioNumber + audioEnd + gateAudio)
        // 
        document.getElementById('start').onended = function () {
          for (let index = 0; index < numberArr.length; index++) {
            setTimeout (function() {
              document.getElementById(`au${index}`).play()
            }, (index + 1)*1000)
          }
        }
        document.getElementById(`au${numberArr.length - 1}`).onended = function () {
          document.getElementById('end').play()
        }
        document.getElementById('end').onended = function() {
          document.getElementById('gateNumber').play()
        }
        document.getElementById('gateNumber').onended = function () {
          vm.isCalling = false
        }
        setTimeout (function () {
          vm.isCalling = true
          document.getElementById('start').play()
        }, 300)
        // 
        item.state = 2
        item.gateNumber = vm.currentGate
        vm.updateStateBooking(item)
      } else {
        alert('Chn bn tip nhn')
      }
    },
    ignoreBooking (item) {
      let vm = this
      vm.currentBooking = ''
      item.state = 3
      vm.updateStateBooking(item)
    },
    updateStateBooking (item) {
      let vm = this
      let filter = item
      vm.$store.dispatch('updateBooking', filter).then(function (result) {
        setTimeout (function () {
          vm.loadBooking()
        }, 500)
      }).catch (function (reject) {
        setTimeout (function () {
          vm.loadBooking()
        }, 500)
      })
    },
    callBack () {
      let vm = this
      vm.callingApplicant(vm.currentBooking)
    },
    receiveBooking (item) {
      let vm = this
      vm.currentBooking = item
      vm.filterCreateDossier = ''
      vm.$store.dispatch('getProcessDetail').then(function (result) {
        let processDetail = result.filter(function (item2) {
          return item2.serviceCode === item.serviceCode
        })[0]
        vm.filterCreateDossier = {
          serviceCode: item.serviceCode,
          govAgencyCode: processDetail['govAgencyCode'],
          dossierTemplateNo: ''
        }
        if (processDetail['options'].length === 1) {
          vm.filterCreateDossier.dossierTemplateNo = processDetail['options'][0]['templateNo']
          vm.postDossier()
        } else {
          vm.listDichVu = processDetail['options']
          vm.dichVuSelected = vm.listDichVu[0]
          vm.dialogAction = true
        }
      }).catch (function (reject) {
      })
    },
    doSubmitCreateDossier () {
      let vm = this
      vm.filterCreateDossier.dossierTemplateNo = vm.dichVuSelected['templateNo']
      vm.postDossier()
      vm.dialogAction = false
    },
    postDossier () {
      let vm = this
      vm.$store.dispatch('postDossier', vm.filterCreateDossier).then(function (result) {
        vm.currentBooking.state = 4
        vm.$store.dispatch('updateBooking', vm.currentBooking).then(function (result1) {
        }).catch (function (reject1) {
        })
        let urlRedirect = '/web/cuc-lanh-su/mot-cua-dien-tu#/danh-sach-ho-so/0/ho-so/' + result.dossierId + '/NEW'
        window.open(urlRedirect, '_blank')
      }).catch (function (reject) {
      })
    },
    updateGateNumber (val) {
      let vm = this
      let filter = {
        gateNumber: val
      }
      vm.$store.dispatch('updateGateNumber', filter).then(function (result) {
        console.log(result)
      })
    },
    getStateName () {
      let vm = this
      return vm.stateList.filter(function(state) {
        return state.stateCode === vm.stateSelected
      })[0]['stateName']
    },
    getColor (level) {
      if (level === 2) {
        return 'green'
      } else if (level === 3) {
        return 'orange darken-1'
      } else if (level === 4) {
        return 'red'
      }
    }
  }
}
</script>
